import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Descarga música para mac"
                meta="El mejor programa para descargar música para mac. Te explicamos que programa hemos elegido y por que. También te damos los enlaces para que te lo descargues"
                description="El mejor programa para descargar música para mac. Te explicamos que programa hemos elegido y por que. También te damos los enlaces para que te lo descargues"
                lang="es"
            />

            <h1>&iquest;Qu&eacute; es la vitamina B12 y qu&eacute; hace?</h1>
            <PostIndex2 linksData={[
                {"url": "#Cantidad", "text": "Cantidad"},
                {"url": "#Alimentos", "text": "Alimentos"},
                {"url": "#Suplementos", "text": "Suplementos"},
                {"url": "#Deficiencia", "text": "Deficiencia"},
            ]}
            />
            <hr/>
            <p class="introductionText">La vitamina B12 forma parte del grupo de <a className="link" href="/vitaminaB">vitaminas B</a>. Es un nutriente que ayuda a mantener sanos los nervios y las c&eacute;lulas
                sangu&iacute;neas del cuerpo y ayuda a fabricar el ADN, el material gen&eacute;tico de todas las
                c&eacute;lulas. La vitamina B12 tambi&eacute;n ayuda a prevenir un tipo de anemia llamada anemia
                megalobl&aacute;stica que hace que la gente se sienta cansada y d&eacute;bil.</p>
            <hr/>
            <p></p>
            <p>Se requieren dos pasos para que el cuerpo absorba la vitamina B12 de los alimentos. Primero,
                el &aacute;cido clorh&iacute;drico en el est&oacute;mago separa la vitamina B12 de la prote&iacute;na a
                la que se une la vitamina B12 en los alimentos. Despu&eacute;s de esto, la vitamina B12 se combina con
                una prote&iacute;na producida por el est&oacute;mago llamada factor intr&iacute;nseco y es absorbida por
                el cuerpo. Algunas personas tienen anemia perniciosa, una condici&oacute;n en la que no pueden producir
                factor intr&iacute;nseco. Como resultado, tienen problemas para absorber la vitamina B12 de todos los
                alimentos y suplementos diet&eacute;ticos.</p>
            <p></p>
            <h2 name={"Cantidad"}>&iquest;Cu&aacute;nta vitamina B12 necesito?</h2>
            <p>La cantidad de vitamina B12 que necesita cada d&iacute;a depende de su edad. Las cantidades diarias
                promedio recomendadas para las diferentes edades se indican a continuaci&oacute;n en microgramos
                (mcg):</p>
            <p></p>

            <div className="dataTable">
                <tbody>
                <tr>
                    <th scope="col">Etapa de vida</th>
                    <th scope="col">Cantidad recomendada</th>
                </tr>
                <tr>
                    <td scope="row">Nacimiento a 6 meses</td>
                    <td align="right">0.4 </td>
                </tr>
                <tr>
                    <td scope="row"><a>Bebes</a> 7-12 meses
                    </td>
                    <td align="right">0.5 </td>
                </tr>
                <tr>
                    <td scope="row">Niños de 1 a 3 años</td>
                    <td align="right">0.9 </td>
                </tr>
                <tr>
                    <td scope="row">Niños de 4 a 8 años</td>
                    <td align="right">1.2 </td>
                </tr>
                <tr>
                    <td scope="row">Niños de 9 a 13 años</td>
                    <td align="right">1.8 </td>
                </tr>
                <tr>
                    <td scope="row">Adolescentes 14-18 años</td>
                    <td align="right">2.4 </td>
                </tr>
                <tr>
                    <td scope="row">Adultos</td>
                    <td align="right">2.4 </td>
                </tr>
                <tr>
                    <td scope="row"> Adolescentes y mujeres embarazadas</td>
                    <td align="right">2.6 </td>
                </tr>
                <tr>
                    <td scope="row">Adolescentes y mujeres lactantes</td>
                    <td align="right">2.8 </td>
                </tr>
                </tbody>
            </div>



            <h2 name={"Alimentos"}>&iquest;Qu&eacute; alimentos proporcionan vitamina B12?</h2>
            <p>La vitamina B12 se encuentra de forma natural en una amplia variedad de alimentos de origen animal y se
                a&ntilde;ade a algunos alimentos fortificados. Los alimentos vegetales no tienen vitamina B12 a menos
                que est&eacute;n fortificados. Usted puede obtener las cantidades recomendadas de vitamina B12 comiendo
                una variedad de alimentos, incluyendo los siguientes:</p>
            <p></p>
            <ul>
                <li>H&iacute;gado de res y almejas, que son las mejores fuentes de vitamina B12.</li>
                <li>Pescado, carne, aves de corral, huevos, leche y otros productos l&aacute;cteos, que tambi&eacute;n
                    contienen vitamina B12.</li>
                <li>Algunos cereales para el desayuno, levaduras nutricionales y otros productos alimenticios que
                    est&aacute;n fortificados con vitamina B12. Para saber si se ha a&ntilde;adido vitamina B12 a un
                    producto alimenticio, consulte las etiquetas del producto.</li>
            </ul>
            <h2 name={"Suplementos"}>&iquest;Qu&eacute; tipos de suplementos diet&eacute;ticos de vitamina B12 hay disponibles?</h2>
            <p>La vitamina B12 se encuentra en casi todas las multivitaminas. Tambi&eacute;n hay disponibles suplementos
                diet&eacute;ticos que contienen s&oacute;lo vitamina B12, o vitamina B12 con nutrientes como
                el &aacute;cido f&oacute;lico y otras vitaminas B. Consulte la etiqueta de informaci&oacute;n sobre los
                suplementos para determinar la cantidad de vitamina B12 suministrada.</p>
            <p></p>
            <p>La vitamina B12 tambi&eacute;n est&aacute; disponible en formas sublinguales (que se disuelven bajo la
                lengua). No hay pruebas de que las formas sublinguales se absorban mejor que las p&iacute;ldoras que se
                ingieren.</p>
            <p></p>
            <p>Se puede administrar una forma prescrita de vitamina B12 en forma de inyecci&oacute;n. Se suele utilizar
                para tratar la deficiencia de vitamina B12. La vitamina B12 tambi&eacute;n est&aacute; disponible como
                medicamento de prescripci&oacute;n en forma de gel nasal que se roc&iacute;a en la nariz.</p>
            <p></p>
            <h2 name={"Cnatidad"}>&iquest;Estoy recibiendo suficiente vitamina B12?</h2>
            <p>La mayor&iacute;a de las personas en los Estados Unidos obtienen suficiente vitamina B12 de los alimentos
                que consumen. Pero algunas personas tienen problemas para absorber la vitamina B12 de los alimentos.
                Como resultado, la deficiencia de vitamina B12 afecta a entre el 1,5% y el 15% del p&uacute;blico. Su
                m&eacute;dico puede analizar su nivel de vitamina B12 para ver si tiene una deficiencia.</p>
            <p></p>
            <p>Es posible que ciertos grupos no obtengan suficiente vitamina B12 o tengan problemas para absorberla:</p>
            <p></p>
            <ul>
                <li>Muchos adultos mayores, que no tienen suficiente &aacute;cido clorh&iacute;drico en el est&oacute;mago
                    para absorber la vitamina B12 presente de forma natural en los alimentos. Las personas mayores de 50
                    a&ntilde;os deben obtener la mayor parte de la vitamina B12 de los alimentos fortificados o los
                    suplementos diet&eacute;ticos porque, en la mayor&iacute;a de los casos, sus cuerpos pueden absorber la
                    vitamina B12 de estas fuentes.</li>
                <li>Las personas con anemia perniciosa cuyos cuerpos no producen el factor intr&iacute;nseco necesario para
                    absorber la vitamina B12. Los m&eacute;dicos suelen tratar la anemia perniciosa con inyecciones de
                    vitamina B12, aunque dosis orales muy altas de vitamina B12 tambi&eacute;n pueden ser eficaces.</li>
                <li>Las personas que se han sometido a una cirug&iacute;a gastrointestinal, como la cirug&iacute;a de
                    p&eacute;rdida de peso, o que tienen trastornos digestivos, como la enfermedad cel&iacute;aca o la
                    enfermedad de Crohn. Estas condiciones pueden disminuir la cantidad de vitamina B12 que el cuerpo puede
                    absorber.</li>
                <li>Algunas personas que comen poco o nada de alimentos de origen animal, como los vegetarianos y los
                    veganos. S&oacute;lo los alimentos de origen animal tienen vitamina B12 de forma natural. Cuando las
                    mujeres embarazadas y las mujeres que amamantan a sus beb&eacute;s son vegetarianas o veganas estrictas,
                    es posible que sus beb&eacute;s tampoco obtengan suficiente vitamina B12.</li>
            </ul>
            <h2 name={"Deficiencia"}>&iquest;Qu&eacute; pasa si no obtengo suficiente vitamina B12?</h2>
            <p>La deficiencia de vitamina B12 causa cansancio, debilidad, estre&ntilde;imiento, p&eacute;rdida de
                apetito, p&eacute;rdida de peso y anemia megalobl&aacute;stica. Tambi&eacute;n pueden producirse
                problemas nerviosos, como entumecimiento y hormigueo en las manos y los pies. Otros s&iacute;ntomas de
                la deficiencia de vitamina B12 incluyen problemas de equilibrio, depresi&oacute;n, confusi&oacute;n,
                demencia, mala memoria y dolor en la boca o la lengua. La deficiencia de vitamina B12 puede da&ntilde;ar
                el sistema nervioso incluso en personas que no tienen anemia, por lo que es importante tratar una
                deficiencia lo antes posible.</p>
            <p></p>
            <p>En los beb&eacute;s, los signos de una deficiencia de vitamina B12 incluyen la falta de crecimiento,
                problemas de movimiento, retrasos en alcanzar los hitos t&iacute;picos del desarrollo y anemia
                megalobl&aacute;stica.</p>
            <p></p>
            <p></p>
            <p>Grandes cantidades de &aacute;cido f&oacute;lico pueden ocultar una deficiencia de vitamina B12
                corrigiendo la anemia megalobl&aacute;stica, un sello distintivo de la deficiencia de vitamina B12. Pero
                el &aacute;cido f&oacute;lico no corrige el da&ntilde;o progresivo del sistema nervioso que
                tambi&eacute;n causa la deficiencia de vitamina B12. Por esta raz&oacute;n, los adultos sanos no
                deber&iacute;an obtener m&aacute;s de 1.000 mcg de &aacute;cido f&oacute;lico al d&iacute;a.</p>
            <p></p>
            <h2>&iquest;Cu&aacute;les son algunos de los efectos de la vitamina B12 en la salud?</h2>
            <p>Los cient&iacute;ficos est&aacute;n estudiando la vitamina B12 para comprender c&oacute;mo afecta a la
                salud. Aqu&iacute; hay varios ejemplos de lo que esta investigaci&oacute;n ha demostrado:</p>
            <p></p>
            <h3>Enfermedades card&iacute;acas</h3>
            <p>Los suplementos de vitamina B12 (junto con el &aacute;cido f&oacute;lico y la vitamina B6) no reducen el
                riesgo de contraer enfermedades cardiovasculares. Los cient&iacute;ficos han pensado que estas vitaminas
                podr&iacute;an ser &uacute;tiles porque reducen los niveles de homociste&iacute;na en la sangre, un
                compuesto relacionado con un mayor riesgo de sufrir un ataque al coraz&oacute;n o un derrame
                cerebral.</p>
            <p></p>
            <h3>Demencia</h3>
            <p>A medida que envejecen, algunas personas desarrollan demencia. Estas personas suelen tener altos niveles
                de homociste&iacute;na en la sangre. La vitamina B12 (con &aacute;cido f&oacute;lico y vitamina B6)
                puede reducir los niveles de homociste&iacute;na, pero los cient&iacute;ficos a&uacute;n no saben si
                estas vitaminas ayudan realmente a prevenir o tratar la demencia.</p>
            <p></p>
            <h3>Energ&iacute;a y rendimiento deportivo</h3>
            <p>Los anuncios a menudo promueven los suplementos de vitamina B12 como una forma de aumentar la
                energ&iacute;a o la resistencia. Excepto en las personas con una deficiencia de vitamina B12, no hay
                pruebas que demuestren que los suplementos de vitamina B12 aumentan la energ&iacute;a o mejoran el
                rendimiento deportivo.</p>
            <p></p>
            <h2>&iquest;Puede la vitamina B12 ser perjudicial?</h2>
            <p>No se ha demostrado que la vitamina B12 cause ning&uacute;n da&ntilde;o.</p>
            <p></p>
            <h2>&iquest;Existe alguna interacci&oacute;n con la vitamina B12 que deba conocer?</h2>
            <p>S&iacute;. La vitamina B12 puede interactuar o interferir con los medicamentos que usted toma y, en
                algunos casos, los medicamentos pueden reducir los niveles de vitamina B12 en el cuerpo. A
                continuaci&oacute;n se incluyen varios ejemplos de medicamentos que pueden interferir con la
                absorci&oacute;n o el uso de la vitamina B12 por parte del cuerpo:</p>
            <p></p>
            <p>Cloranfenicol (Cloromicetina&reg;), un antibi&oacute;tico que se utiliza para tratar ciertas
                infecciones.</p>
            <p>Inhibidores de la bomba de protones, como el omeprazol (Prilosec&reg;) y el lansoprazol (Prevacid&reg;),
                que se utilizan para tratar el reflujo &aacute;cido y la enfermedad de &uacute;lcera p&eacute;ptica.</p>
            <p>Antagonistas de los receptores de la histamina H2, como la cimetidina (Tagamet&reg;), la famotidina
                (Pepcid&reg;) y la ranitidina (Zantac&reg;), que se utilizan para tratar la enfermedad de &uacute;lcera
                p&eacute;ptica.</p>
            <p>Metformina, un f&aacute;rmaco utilizado para tratar la diabetes.</p>
            <p>Informe a su m&eacute;dico, farmac&eacute;utico y otros proveedores de atenci&oacute;n m&eacute;dica
                sobre los suplementos diet&eacute;ticos y los medicamentos que toma. Ellos pueden decirle si esos
                suplementos diet&eacute;ticos pueden interactuar o interferir con sus medicamentos recetados o de venta
                libre o si los medicamentos pueden interferir con la forma en que su cuerpo absorbe, usa o descompone
                los nutrientes.</p>
            <p></p>
            <h2>La vitamina B12 y la alimentaci&oacute;n saludable</h2>
            <p>La gente deber&iacute;a obtener la mayor parte de sus nutrientes de los alimentos, aconseja el gobierno
                federal en las Gu&iacute;as Diet&eacute;ticas para los Americanos. Los alimentos contienen vitaminas,
                minerales, fibra diet&eacute;tica y otras sustancias que benefician la salud. En algunos casos, los
                alimentos fortificados y los suplementos diet&eacute;ticos pueden proporcionar nutrientes que de otra
                forma se consumir&iacute;an en cantidades inferiores a las recomendadas. Para obtener m&aacute;s
                informaci&oacute;n acerca de c&oacute;mo construir una dieta saludable, consulte las Gu&iacute;as
                Alimentarias para Americanosexternos y la cl&aacute;usula de exenci&oacute;n de responsabilidad del
                enlace MyPlateexternal del Departamento de Agricultura de los Estados Unidos.</p>
            <p></p>
            <div style={{height: `300px`}}>
            </div>
        </Layout>
    )
}

export default Mac
